@import 'constants/index.scss';

.container {
  margin: 0 auto;
  max-width: 335px;
  padding-left: 20px;
  padding-right: 20px;

  @media only screen and (min-width: $media-screen-mobile) {
    max-width: 706px;
  }

  @media only screen and (min-width: $media-screen-tablet) {
    max-width: 1200px;
  }
}

img {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

.cover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.color-primary {
  color: $color-primary;
}